html {
	height: 100%;
}

body {
	height: 100%;
	background-color: blue;
	background-color: #00BFFF;
	
}

#header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	color: orange;
	color: #FF580F;
	margin-bottom: 5em;
	text-decoration: underline;
}

#drum-machine {
	width: 100%;
}

#display {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;	
}

input {
	height: 2em;
	width: 8em;
	border: solid;
	border-color: orange;
	border-color: #FF580F;
	pointer-events: none;  
	text-align: center;
	font-size: 2em;
	color: #FF580F;
	background-color: white;
}

#drumKeys {
	position: relative;
	top: 0.5em;

}

.row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
}

.drum-pad {
	padding-right: 2em;
	padding-left: 2em;
	color: white;
	border: solid;
	border-width: 0.25em;
	height: 5.9em;
	width: 3em;
	text-align: center;
	background-color: orange;
	background-color: rgba(255,88,15);
	background-color: #FF580F;
	cursor: pointer;
}

h1 {
	pointer-events: none;
}

#footer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	text-align: center;
	margin-top: 11em;
	color: #FF580F;
}





